import React from 'react';
import './HomeTab.css';
import { ArrowsAngleExpand } from 'react-bootstrap-icons'; 
import { ChevronRight } from 'react-bootstrap-icons';
import { CheckSquare } from 'react-bootstrap-icons';



class HomeTab extends React.Component {
    constructor() {
        super();
        this.state = {
            route: ''
        };
    }

    render() {
        return (
          <div>
                {/* Parent Tasks */}
                <div>
                    <div className='menuBtnContainer'>
                        <div className='menuBtn'>
                            <div>
                            My Tasks
                            <span className='taskCount'>0</span>
                            </div>
                            
                            <button className='iconBtn' onClick={() => alert('clicked expand')}>
                                        <ArrowsAngleExpand className='icon' size={20} />
                            </button>
                        </div>
                    </div>
                </div>
 
                {/*Student Tasks*/}
                <div>
                     {/* Student Name */}
                    <div className='menuBtnContainer'>
                        <div className='menuBtn menuBtnGray'>
                            <div>
                            Thomas
                            <span className='taskCount'>3</span>
                            </div>
                            
                            <button className='iconBtn' onClick={() => alert('clicked expand')}>
                                        <ArrowsAngleExpand className='icon' size={20} />
                            </button>
                        </div>
        
                        {/*Task */}
                        <div className='menuBtnExpanded'>
                            <div>
                            <p>Math</p>
                            <p>Lesson 1</p>
                            </div>
                    
                            <div>
                            <button className='iconBtn' onClick={() => alert('clicked complete')}>
                                        <CheckSquare className='icon' size={25} />
                            </button>
                            <button className='iconBtn' onClick={() => alert('view from')}>
                                        <ChevronRight className='icon' size={25} />
                            </button>
                            </div>
                        </div>
                    </div>

                    {/* Student Name #2 */}
                    <div className='menuBtnContainer'>
                        <div className='menuBtn menuBtnGray'>
                            <div>
                            Titus
                            <span className='taskCount'>3</span>
                            </div>
                            
                            <button className='iconBtn' onClick={() => alert('clicked expand')}>
                                        <ArrowsAngleExpand className='icon' size={20} />
                            </button>
                        </div>
        
                        {/*Task #2 */}
                        <div className='menuBtnExpanded'>
                            <div>
                            <p>Spelling</p>
                            <p>Lesson 3</p>
                            </div>
                    
                            <div>
                            <button className='iconBtn' onClick={() => alert('clicked complete')}>
                                        <CheckSquare className='icon' size={25} />
                            </button>
                            <button className='iconBtn' onClick={() => alert('view form')}>
                                        <ChevronRight className='icon' size={25} />
                            </button>
                            </div>
                        </div>
                    </div>
                </div>
          </div>
         
        
        )
    }
}

export default HomeTab;